				"id": "/Users/seth/src/posm/posm.github.io/src/pages/preparing-for-the-field/index.md absPath of file >>> MarkdownRemark",
				"html": "<p>Now you have a POSM. The device has all the core software ready to go, and the\nnext step is to create a “deployment” - this means downloading all the\nOpenStreetMap data and other resources for your area of interest (AOI). You\nshould do this in an area with a stable internet connection - if you are\ntraveling for fieldwork, we highly recommend doing this before you leave. POSM\ncan handle relatively large deployments (entire districts and sometimes\ncountries).</p>\n<h2>Creating an AOI deployment</h2>\n<p>On your computer, when connected to the internet, navigate in your browser to\n<a href=\"http://export.posm.io/en/\">the POSM export tool</a>. This site will allow you to\nselect a geographic area and extract the OSM-related data you need for it.</p>\n<p>First, long in with your OpenStreetMap credentials. You’ll arrive by default at\nthe “Create” page. You can set the area by searching for a place, by geographic\ncoordinates, or by zooming in on the map and physically drawing a box around\nyour project area.</p>\n<p><img src=\"/guide/preparing-for-the-field/aoi_description.png\"></p>\n<p>Once you’ve entered in some basic project info on the left side of the screen,\nthen click on “File Formats” in the navigation menu.</p>\n<p><img src=\"/guide/preparing-for-the-field/export_formats.png\"></p>\n<p>This tool was designed to enable a variety of mapping analysis and data uses, so\nyou won’t need to check all the boxes - <strong>all that’s required for POSM is the\nOSM PBF file</strong> (the OSMAnd OBF is also highly recommended). But just in case,\nhere’s a run-down of what your options are and what you might use them for if\nyou do want extra data available offline:</p>\n<ul>\n<li>Esri SHP (OSM Schema): Standard shapefile used by most GIS applications. This\nextracts all OSM data into three shapefiles (point, line, and polygon)</li>\n<li>Esri SHP (Thematic Schema): Standard shapefile used by most GIS applications.\nThis extracts the OSM data into many shapefiles (e.g. waterways, buildings,\nhealth/schools, residential landuse areas, airports, amenity points, etc).\nThis is probably more useful if you are building a base map or need basic GIS\ndata layers for the area.</li>\n<li>Garmin Map: For loading onto GPS devices</li>\n<li>Google Earth KMZ: For loading into Google Earth. Often used for tracks.</li>\n<li>MBTiles: Background map tiles for offline use. Extracting these will allow you\nto host offline mapathons with the HOT tasking manager. Check this box and\nenter the Bing API when prompted:\n<code>https://ecn.t2.tiles.virtualearth.net/tiles/a{q}.jpeg?g=587&#x26;mkt=en-gb&#x26;n=z</code>.\nMake sure the <code>{q}</code> copies and pastes properly - it sometimes gets replaced\nwith other characters. Once you’ve pasted the link, you’ll also need to set\nthe minimum and maximum zoom levels; we recommend 13 to 20.\n<em>Note: These MBTiles aren’t an effective substitute for the tiles needed in\nthe OpenMapKit app; you will still generate these using POSM.</em></li>\n</ul>\n<p><img src=\"/guide/preparing-for-the-field/export_mbtiles.png\"></p>\n<ul>\n<li>OSMAnd OBF: For loading onto the OsmAnd offline map app for Android phones.\nThis app is really useful for offline navigation and wayfinding during\nfieldwork.</li>\n<li>OSM PBF: This is the core OSM data package. This is the only absolute\nrequirement for POSM.</li>\n<li>OSM XML: This is unnecessary and will be removed from the listing shortly.</li>\n<li>SQLite SQL: OSM data for working with in a database format</li>\n</ul>\n<p>Once you’ve made your selections, run the export. This can take some time -\nyou’ll get an email when it is complete.</p>\n<p>You can download the individual components or the whole bundle, but the easiest\nway to get the export onto the POSM is by looking for the “POSM Bundle”\ndownload, right-clicking that, and copying the link. You will use this link to\ndownload the bundle directly onto the POSM.</p>\n<p><img src=\"/guide/preparing-for-the-field/export_bundle.png\"></p>\n<h2>Moving the AOI onto the POSM</h2>\n<p>Plug the POSM into an ethernet connection and power it up. POSM will broadcast a\nlocal wifi network named “POSM”. The password is <code>awesomeposm</code>.</p>\n<p>Use your computer to connect to the POSM wifi. In your browser, navigate to\nposm.io (any http:// website will also redirect to posm.io).</p>\n<p>Click on the admin console, then enter the URL from the “POSM Bundle” link in\nyour data export (it will end with <code>.tar.gz</code>. Click “Start”. You will see text\nappear in the console output, letting you know it is running. Behind the scenes,\nPOSM is downloding the OSM data and setting it up for use. This can take some\ntime.</p>\n<p><img src=\"/guide/preparing-for-the-field/aoi_deployment.png\"></p>\n<p>When the POSM extract is ready, you can unplug the ethernet cable - POSM has\neverything it needs to go offline.</p>\n<h2>Creating MBTiles</h2>\n<p>There’s one last thing you need to do to set up POSM. Go back to the posm.io\nhomepage, and click on the FieldPapers link. You need to create a FieldPapers\natlas for the area you’re working in - this triggers the creation of MB tiles,\nwhich are needed for an OMK deployment. These are the zoomable map tiles in the\nbackground of the OpenMapKit app; the OSM data sits on top of this as\ninteractive buildings, roads, etc. It can take a couple of hours to create the\nMB tiles, but this can run in the background as long as POSM is plugged in and\npowered up.</p>\n<p>Field Papers process:</p>\n<p><img src=\"/guide/preparing-for-the-field/fieldpapers.png\"></p>\n<p><img src=\"/guide/preparing-for-the-field/fieldpapers_aoi.jpg\"></p>\n<p><img src=\"/guide/preparing-for-the-field/fieldpapers_rendering.png\"></p>\n<p>Note: if you are doing repeat cycles of mapping, then every time you make OSM\nedits using OMK, iD, JOSM, etc, you will need to re-run the FieldPapers atlas in\norder to create updated MB tiles for your area. This means that your second and\nthird round of mapping will build off previous changes.</p>\n<h2>What next?</h2>\n<p>This set-up procedure includes everything that must be done with an internet\nconnection. Everything else can be run offline, but we highly suggest testing\nthe entire workflow before going to the field to allow time for troubleshooting.</p>",
